import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Button, FormControl, FormLabel, Input, Select
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { fetchPlaidToken, pullFund } from '../../../store/middlewares/account';
import { apiErrorHandler, showNotification } from '../../../utils/general';
import { updatePullFundData } from '../../../store/actionCreators/account';
import CurrencyInput from '../../../components/CurrencyInput';
import OpenPlaidLink from '../../../components/Plaid';
import { useDrawer } from '../../../hooks/drawer';

const PullFundsSuccess = React.lazy(() => import('../PullFundsSuccess/PullFundsSuccess'));

/**
 * Pull funds form page UI
 * @returns {React.ReactNode}
 */
const PullFundsInfo = () => {
  const { t } = useTranslation();
  const { data: { option, contactId } = {}, open } = useDrawer<{option: 'bank' | 'card', contactId: string}>();
  const { selectedAccount: account, selfBankAccounts, linkedCards } = useAppSelector((store) => store.account, shallowEqual);

  const dispatch = useAppAsyncDispatch();

  const [selectedContact, selectContact] = useState(contactId);
  const [amount, updateAmount] = useState('');
  const [description, updateDesc] = useState('');

  const [plaidToken, updatePlaidToken] = useState<string | null>(null);

  const isValid = useMemo(() => amount !== '' && description !== '' && selectedContact !== '', [selectedContact, amount, description]);

  const reLinkAccount = useCallback(() => {
    if (account && selectedContact) {
      dispatch(fetchPlaidToken(account.id, {
        packageName: 'web',
        plaidUpdateMode: {
          type: 'itemLoginRequired',
          contactId: selectedContact
        }
      })).then((data) => {
        updatePlaidToken(data.linkToken);
      });
    }
  }, [account, selectedContact]);

  // eslint-disable-next-line camelcase
  const plaidOnSuccess = useCallback((public_token: string, metadata: any) => {
    console.log(public_token, metadata);
  }, []);

  const onSubmit = useCallback(() => {
    if (isValid && account && selectedContact) {
      dispatch(pullFund({
        accountId: account.id,
        amount,
        contactId: selectedContact,
        description
      }, option))
        .then(() => {
          const selectedContactDetails: any = option === 'bank' ? selfBankAccounts.find((b) => b.id === selectedContact) : linkedCards.data.find((b) => b.id === selectedContact);
          dispatch(updatePullFundData({
            label: option === 'bank' ? `${selectedContactDetails?.ach?.bankName} XXXXXX ${selectedContactDetails?.ach?.accountNumber.slice(selectedContactDetails.ach.accountNumber.length - 4)}` : `XXXX XXXX XXXX ${selectedContactDetails.debitCard?.last4}`,
            amount,
            description
          }));
          setTimeout(() => {
            open(PullFundsSuccess, {
              option
            });
          }, 0);
        })
        .catch((error: any) => {
          console.log(error);
          //  error.response.data.code === 'EC_PLAID_ACCESS_TOKEN_EXPIRED'
          if (option === 'bank' && (error.response.data.code === 'EC_PLAID_LINK_MODE_UPDATE_ITEM_LOGIN_REQUIRED')) {
            showNotification('An error occurred.', 'Plaid connection expired. You need to reconnect your bank account.', 'error');
            // need to relink the account
            setTimeout(() => {
              reLinkAccount();
            }, 1000);
          } else {
            apiErrorHandler(error);
          }
        });
    }
  }, [option, selectedContact, account, isValid, amount, description]);

  return (
    <div className="page">
      <MiniBox
        heading={t('pull_funds_info.title')}
        footer={(
          <Button width="100%" variant="primary" onClick={onSubmit} disabled={!isValid}>
            {t('pull_funds_info.ctaText')}
          </Button>
)}
      >
        <Box width="100%" paddingTop="15px">
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('pull_funds_info.source')}
            </FormLabel>
            <Select
              variant="primary"
              value={selectedContact}
              onChange={(e: React.ChangeEvent<HTMLSelectElement>) => selectContact(e.target.value)}
            >
              {option === 'bank' ? selfBankAccounts.map((b) => (
                <option key={b.id} value={b.id}>
                  {b.ach.bankName}
                  {' '}
                  XXXXXX
                  {' '}
                  {b.ach.accountNumber.slice(b.ach.accountNumber.length - 4)}
                </option>
              )) : linkedCards.data.map((c) => (c.debitCard
                ? (
                  <option key={c.id} value={c.id}>
                    XXXX XXXX XXXX
                    {' '}
                    {c.debitCard?.last4}
                  </option>
                ) : null
              ))}

            </Select>
          </FormControl>
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('pull_funds_info.destination')}
            </FormLabel>
            <Input
              disabled
              value={account?.label}
              variant="primary"
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('pull_funds_info.amount')}
            </FormLabel>
            <CurrencyInput
              value={amount}
              onChange={(v) => {
                updateAmount(v || '');
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('pull_funds_info.description')}
            </FormLabel>
            <Input
              required
              value={description}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => updateDesc(e.target.value)}
              variant="primary"
            />
          </FormControl>
        </Box>
      </MiniBox>

      {plaidToken ? (
        <OpenPlaidLink
          token={plaidToken}
          onSuccess={plaidOnSuccess}
          onExit={() => {
            updatePlaidToken(null);
          }}
        />
      ) : null}

    </div>
  );
};

export default PullFundsInfo;
